
import { Component, Prop, Emit, Vue } from 'vue-property-decorator';
interface CustomItem {
  label: string,
  prop: string,
  readonly?: boolean
}
@Component
export default class CustomListItem extends Vue {
  @Prop({default: false}) private visible!: boolean;
  @Prop({default: 'host'}) private type!: string;
  @Prop({default: () => []}) private all_column_item!: Array<CustomItem>;
  @Prop({default: () => []}) private all_item!: Array<any>;
  @Prop({default: false}) private newView!: boolean;
  private select_item: Array<string> = [];
  private typeObj={
    'host':{func:'SET_CUSTOM_HOST',value:'custom_host'},
    'nas':{func:'SET_NAS_HOST',value:'nas_host'},
    'cluster':{func:'SET_CLUSTER_HOST',value:'pod'},
    'pod_host':{func:'SET_HOST_LIST',value:'pod'},
    'pod_vm':{func:'SET_VM_HOST',value:'pod'},
  }
  private created() {
    if(this.type === 'cluster'){
      this.FnHandleSelectItem(this.$store.state[this.typeObj[this.type].value].cluster_host)
    }else if(this.type === 'pod_host'){
      this.FnHandleSelectItem(this.$store.state[this.typeObj[this.type].value].host_list)
    } else if(this.type === 'pod_vm'){
      this.FnHandleSelectItem(this.$store.state[this.typeObj[this.type].value].vm_host)
    } else {
      this.FnHandleSelectItem(this.$store.state[this.typeObj[this.type].value])
    }
  }
  private FnHandleSelectItem(item) {
    if (item.length>0) {
      this.select_item = [...item];
    } else {
      this.select_item = this.all_column_item.slice(0,25).map(item=>item.label)
    }
    this.FnEmit()
  }
  private FnConfirm() {
    if(this.select_item.length<5 || this.select_item.length>25){
      this.$message.warning("自定义列表项最少为5项，最多为25项")
      return;
    }
    this.$store.commit(this.typeObj[this.type].func, this.select_item)
    this.FnEmit()
    this.FnClose()
  }
  private FnClose() {
    this.$emit('update:visible', false)
  }
  @Emit('fn-custom')
  public FnEmit() {
    return this.select_item
  }
}
